html {
    font-size: 62.5%;
}

.body {
    background-color: white;
    font-family: $primaryFont;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

.container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 0 11rem;
    width: 100%;

    @media screen and (max-width: 1200px) {
        padding: 0 3rem;
    }
}

.page__title {
    font-size: 6rem;

    @media screen and (max-width: 768px) {
        font-size: 3.6rem;
    }
}

@import "../components/App/app.scss";
@import "../components/Header/header.scss";
@import "../components/FirstBlock/FirstBlock.scss";
@import "../components/SecondBlock/SecondBlock.scss";
@import "../components/Footer/footer.scss";
